

*{
    margin:0;
    padding:0;
    font-weight:bold;
}


.home-page{
    display:flex;
    flex-direction:column;
    width:100%;
    height:100%;/*100% of the total height of the respective elements in the div */
    background-image: linear-gradient(to bottom right, #05081a, #7e0707);
    background-size:cover;
    scroll-snap-type: y mandatory;
    scroll-behavior:smooth;
    overflow-y:auto;
    color:white;
   
    
    

}
::-webkit-scrollbar {
    width:0px;/*can manipulte the width of the scrollbar if needed */
}



